@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  padding-top: 1rem;
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

.app:before {
  content: '';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
  outline: none;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 90%;
  height: auto;
  margin: auto;
  padding: 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 60%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
  margin-top: 8rem;
  gap: 40px;
}

.bold {
  font-weight: 700;
}

.search button {
  padding: 10px 20px;
  background-color: rgba(255,255,255, 0.2);
  border: 1px solid white;
  color: white;
  border-radius: 5rem;
  margin-left: 1rem;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 4rem;
  }

  p {
    font-size: 1.4rem;
  }

  .container {
    max-width: 95%;
    top: 5%;
  }

  .bottom {
    flex-direction: column;
    gap: 20px;
    margin-top: 1rem;
    width: 80%;
  }

  .search button {
    margin-top: 1rem;
    margin-left: 0;
  }
  .app input{
    margin-right: 1rem;
  }
}

@media screen and (max-width: 480px) {
  h1 {
    font-size: 3rem;
  }

  p {
    font-size: 1.2rem;
  }

  .container {
    max-width: 100%;
    top: 5%;
  }

  .bottom {
    margin-top: 1rem;
    width: 80%;
    
  }
  .bottom p{
    font-size: 5.4vw;
  }

  .search button {
    margin-top: 1rem;
    margin-left: 0;
  }
}
